.container {
  display: flex;
  flex-direction: column;
  padding-top: 28px;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.6)
  );
}

.container-cotnrols {
  display: flex;
  padding: 10px 15px;
	align-items: center;
	justify-content: space-between;
}

.container-cotnrols .row{
	display: flex;
	align-items: center;
	height: 100%;
}